* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #200042;
    color:#fff;
}

.header {
    background-color: #4e2d9b;
    padding: 20px;
}
.headerBlock {
    text-align: center;
}
.headerButton {
    border: 1px silver;
    border-radius: 15px;
    background-color: #8c23ff;
    padding: 10px 20px;
    margin: 20px;
    color:#fff;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
}
.headerButton:hover {
    border-color: silver;
    border-width: 10px;
    border-radius: 15px;
    background-color: #5b22cd;
    padding: 10px 20px;
    margin: 20px;
    cursor: pointer;
}
.login {
    border-color: blue;
    border-width: 10px;
    text-align: center;
    margin-top: 15%;
    width: fit-content;
    background: indigo;
    padding: 50px;
}
.reg {
    border-color: blue;
    border-width: 10px;
    text-align: center;
    margin-top: 8%;
    width: fit-content;
    background: indigo;
    padding: 50px;
}
.loginInput {
    width: 250px;
    height: 40px;
    text-align: center;
    color: white;
    border-width: 2px;
    border-color: black;
    border-radius: 15px;
    font-family: Calibri;
    font-size: larger;
    cursor: pointer;
    background: #8c23ff;
    text-decoration: none;
    padding: 2px;
}
h1 {
    text-align: center;
    font-family: Calibri;
}
h3 {
    text-align: center;
    font-family: Calibri;
}
.loginBox {
    width:100%;
    display: flex;
    justify-content: center;
}
::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color: white;
}
.chatItem {
    text-align: center;
    padding: 10px;
    width: 250px;
    height: 60px;
    cursor: pointer;
    background: #8c23ff;
    color: white;
    font-family: Calibri;
    font-size: larger;
    margin-bottom: 10px;
    border-width: 5px;
    border-color: cornflowerblue;
    border-radius: 20px;
}
.chatItem:hover {
    background: #5b22cd;
}
.chatsTable {
    margin: 5%;
    border-color: #4e2d9b;
    border-width: 5px;
}
.chatsTableBlock {
    float: left;
    display: block;
    width: 25%;
    overflow-y: auto;
    height: 700px;
    position: fixed;
    margin-top: 5%;
}
.chatBlock {
    float: right;
    display: block;
    width: 60%;
    margin: 5%;
    height: 700px;
}
.scrollingChat {
    height: 600px;
    overflow: auto;
}
.inputInChat {
    width: 89%;
    font-size: large;
    font-family: Calibri;
    background: #5b22cd;
    color: white;
}
.buttonInChat {
    width: 9%;
    margin-left: 1%;
    font-family: Calibri;
    font-size: large;
    background: #4e2d9b;
    color: white;
}
.MessageChat {
    background: #5b22cd;
    padding: 10px;
    border-radius: 15px;
    width: fit-content;
}
.UserNameInChat {
    padding: 5px;
    background: #4e2d9b;
    border-radius: 10px;
}
.MessageInChat {
    padding: 5px;
}
.MessageItem {
    margin-top: 2px;
    margin-bottom: 2px;
}
.MediaInChat {
    margin-left: 10px;
}